body {
    font-family: sans-serif;
   -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
 text-rendering: optimizeLegibility;
 font-smooth: always;
    margin: 0;
    padding: 0;
}

h1 {
   font-size: 3rem;
   letter-spacing: 1px;
   margin: 0;
   padding: 0;
   text-align: center;
   opacity: 0.5;
}

.app-outer {
   align-items: center; 
   bottom: 0;
   display: flex;
   flex-direction: column;
   justify-content: center;
   left: 0;
   padding: 20px 0;
   position: absolute;
   right: 0;
   top: 0;
}

.app-inner {
   display: flex;
   flex-direction: column;
   height: 100%;
   max-width: 1000px;
   width: 100%;
}

.tag-cloud {
   flex: 1 1;
}

.tag-cloud > div {
   -webkit-transition: 1.4s;
   transition: 1.4s;
}

.tag-item-wrapper {
   cursor: pointer;
}

.tag-item-wrapper:hover {
   z-index: 100;
}

.tag-item-wrapper:hover .tag-item-tooltip {
   display: block;
}

.tag-item-tooltip {
   background: #c01916;
   color: white;
   margin-top: 5px;
 z-index: 100;
 display: none;
   padding: 15px;
   font-size: 14px;
   line-height: 1;
   position: absolute;
   min-width: 60px;
   left: 50%;
   -webkit-transform: translateX(-50%);
           transform: translateX(-50%);
 text-align: center;
}
